<template>
  <Field as="select"
         :id="id"
         :ref="id"
         :name="name"
         :title="title"
         :rules="rules"
         v-bind="$attrs"
         v-model="mv">

    <option value=""
      :disabled="(defaultValueDisabled ? 'disabled' : false)"
      selected v-if="defaultValue">{{ defaultValue }}
    </option>
    
    <template v-if="data && data.length > 0">
      <option v-for="d,i in data"
              :key="i"
              :value="d.value"
              :selected="d.value === selectedValue">{{ d.text }}</option>
    </template>
  </Field>

  <ErrorMessage class="form-field-error" :name="name">
  </ErrorMessage>
</template>

<script>
import { Field, ErrorMessage } from 'vee-validate';

export default {
  name: 'ui-select',
  components: {
    Field,
    ErrorMessage,
  },
  props: {
    /** select id */
    id: String,
    /** select name */
    name: String,
    /** select title */
    title: String,
    /** select validation rules */
    rules: String,
    /** select default value */
    defaultValue: [String, Number],
    /** select selected value */
    selectedValue: [String, Number],
    /** select data */
    data: Array,
    /** select default value   ( false - ,  true) */
    defaultValueDisabled: Boolean,
    /** select modelValue */
    modelValue: String,
  },
  computed: {
    mv: {
      get() {
        return this.modelValue;
      },
      set(v) {}
    }
  },
}
</script>

<style lang="scss" scoped>

</style>