import { createApp } from 'vue';
import App from './App.vue';
import router from '@/routes/router';
import store from '@/store/store';
import axios from 'axios';
import { initDefineRule } from '@/utils/validation/validation';
import { commonComputed } from '@/store/helper';
import moment from 'moment';
import messageUtil from '@/utils/ui/messageUtil';
import { uiModule } from '@/plugins/module/ui/ui';
import { formModule } from '@/plugins/module/form/form';
import { $vfm } from 'vue-final-modal';
import { vfmPlugin } from 'vue-final-modal';

import '@/assets/scss/style.scss';
import { isNotBlank } from '@/utils/util';

initDefineRule();

moment.locale('kr');

const app = createApp({
  extends: App,
  async created() {
    const token = localStorage.getItem('token');
    if (token) {
      this.$store.commit('Auth/SET_TOKEN', token);
    }

    this.$http.interceptors.response.use(
      response => response,
      async error => {
        if (isNotBlank(error.response.data.type) && 'BIZ' === error.response.data.type) {
          //    
          messageUtil.toastError(error.response.data.message);
        } else {
          messageUtil.toastError(' .');
        }

        //  , JWT /   
        if (401 === error.response.status) {
          const res = await this.$store.dispatch('Auth/LOGOUT', this.$http);
          if ('ok' === res) {
            this.$router.push({
              path: '/',
              query: {
                e: this.$moment().format('YYYYMMDDHHmmss'),
              },
            });
          }
        }
        // 404 
        if (404 === error.response.status) {
          this.$router.push('/error');
        }

        return Promise.reject(error);
      }
    );
  }
});

app.mixin({
  computed: {
    ...commonComputed
  },
});

app.config.globalProperties.$rootUrl = window.location.origin;
app.config.globalProperties.$nowUrl = window.location.href;
app.config.globalProperties.$http = axios.create({
  baseURL: '/api',
  headers: {
    'Cache-Control': 'no-cache',
  }
});
app.config.globalProperties.$moment = moment;
app.config.globalProperties.$modal = $vfm;

// Production Tip ()
app.config.warnHandler = (msg, vm, trace) => {
  return null;
};

app.use(store)
   .use(router)
   .use(vfmPlugin)
   .use(uiModule)
   .use(formModule)
   .mount('#app');
