<template>
  <Field type="checkbox"
         :name="name"
         :id="id"
         :ref="id"
         :title="title"
         :rules="rules"
         :value="value || getValueByIdx(0)"
         :unchecked-value="getValueByIdx(1)"
         v-bind="$attrs"
         v-model="mv"
         @change="onChange($event)">
  </Field>

  <label v-if="label" :for="id">
    <span class="sr-only" v-if="labelHidden">{{ label }}</span>
    <template v-else>{{ label }}</template>
  </label>

  <ErrorMessage class="form-field-error" :name="name">
  </ErrorMessage>
</template>

<script>
import { isBlank } from '@/utils/util';
import { Field, ErrorMessage } from 'vee-validate';

export default {
  name: 'ui-checkbox',
  components: {
    Field,
    ErrorMessage,
  },
  props: {
    /** checkbox id */
    id: String,
    /** checkbox name */
    name: String,
    /** checkbox title */
    title: String,
    /** checkbox checked */
    checked: Boolean,
    /** checkbox label */
    label: String,
    /** checkbox label hidden */
    labelHidden: Boolean,
    /** checkbox validation rules */
    rules: String,
    /** checkbox modelValue */
    modelValue: {
      default: '',
    },
    /** checkbox true value */
    value: {
      type: [String, Number],
      default: undefined,
    },
    /** checkbox true value & false value */
    values: String,
  },
  computed: {
    mv: {
      get() {
        return this.modelValue;
      },
      set(v) {}
    }
  },
  methods: {
    /** change  */
    onChange(e) {
      this.$emit('update:modelValue', e.target.value);
    },
    /**    value  */
    getValue(e) {
      if (e.target.checked) {
        if (isBlank(this.values)) return '';
        return this.values.split(',')[0];
      }
      return this.values.split(',')[1];
    },
    /** index value  */
    getValueByIdx(i) {
      if (isBlank(this.values)) return '';
      return this.values.split(',')[i];
    },
  },
}
</script>

<style lang="scss" scoped>

</style>